.news{
  background-color: $bColor;
  
 
 &-header{
   display: flex;
   justify-content: space-between;
   align-items: center;
 }
 &-ttl{
   color: white;
 }
 &-btn{
   @include _btn('small');
   &:hover{
     @include _hover;
   }
 }
 &-area{
   margin-top: 20px;
 }
 &-item{
   width: 100%;
   background-color: white;
   &:nth-of-type(n+2){
     margin-top: 10px;
   }  
   
   a{
     display: flex;
     align-items: center;
     padding: 20px;
     @include mq{
     padding: 10px;
    }
     &:hover{
       @include _hoverB;
     }
   }
  }
  &-date{
    @include fz(16);
    margin-right: 10px;
    @include mq{
      @include fz(14);
    }
  }
  &-heading{
    @include fz(16);
    @include mq{
      @include fz(14);
    }
   
  }

}